*,
*::after,
*::before {
  box-sizing: border-box;
}
.document {
  padding: 0;
  margin: 0;
}
.page {
  display: flex;
  flex-direction: column;
  padding: 0;
  margin: 0;
  font-family: "Coolvetica", "Arial", sans-serif;
}
@font-face {
  font-family: "Coolvetica";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Coolvetica.woff2") format("woff2"), url("../fonts/Coolvetica.woff") format("woff");
}
@keyframes letter {
  0% {
    transform: scale3d(1, 1, 1);
  }
  20% {
    transform: scale3d(1.4, 0.7, 1);
  }
  60% {
    transform: scale3d(1.3, 1.6, 1);
  }
  100% {
    transform: scale3d(1, 1, 1);
  }
}
@keyframes nav-link {
  0% {
    transform: scale3d(0, 0, 0);
  }
  20% {
    transform: scale3d(1.3, 0.8, 1);
  }
  50% {
    transform: scale3d(0.9, 1.1, 1);
  }
  80% {
    transform: scale3d(1.1, 0.8, 1);
  }
  100% {
    transform: scale3d(1, 0.9, 1);
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.header {
  min-height: 100vh;
  background-image: url("../img/header-blop.svg");
  background-size: 1054px 761px;
  background-repeat: no-repeat;
  background-position: top left;
}
.header__logo {
  transition: 0.3s;
}
.header__logo:hover {
  opacity: 0.6;
}
.header__logo:active {
  opacity: 0.3;
}
.navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 40px 100px;
}
.navigation__list {
  display: flex;
  padding: 0;
  gap: 50px;
  list-style-type: none;
}
.navigation__link {
  color: #ffffff;
  text-decoration: none;
  opacity: 0.6;
  transition: 0.3s;
}
.navigation__link:hover {
  opacity: 1;
}
.navigation__link--active {
  position: relative;
  color: #000c24;
  z-index: 1;
  opacity: 1;
}
.navigation__link--active::before {
  position: absolute;
  top: -150%;
  transition: 0.5s;
  left: -30%;
  display: block;
  width: 200%;
  height: 98px;
  background-image: url("../img/link-active.svg");
  background-size: 100% 98px;
  background-position: center;
  background-repeat: no-repeat;
  content: "";
  z-index: -1;
  animation-name: nav-link;
  animation-duration: 0.6s;
  animation-timing-function: cubic-bezier(0.47, 0.82, 0.17, 1);
  animation-fill-mode: forwards;
}
.hero {
  padding-left: 80px;
  padding-top: 40px;
}
.hero__title {
  margin: 0;
  color: #ffffff;
  font-size: 50px;
  letter-spacing: -5px;
}
.hero__letter {
  display: inline-block;
  transition: 0.15s;
}
.hero__letter:hover {
  color: #246dff;
}
.hero__description {
  margin: 40px 0;
  color: #ffffff;
  font-size: 18px;
}
.hero__social-list {
  display: flex;
  padding: 0;
  gap: 30px;
  list-style-type: none;
}
.hero__social-button {
  color: #ffffff;
  transition: 0.3s;
}
.hero__social-button:hover {
  color: #246dff;
}
.hero__social-button:active {
  opacity: 0.6;
}
.animated {
  animation-name: letter;
  animation-duration: 0.6s;
  animation-timing-function: cubic-bezier(0.47, 0.82, 0.17, 1);
  animation-fill-mode: forwards;
}
.main {
  flex-grow: 1;
}
